body {
  font-family: 'Proxima Nova', sans-serif !important;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  font-weight: 400; }

.wrapper-for-bg {
  overflow: hidden; }

@media (max-width: 768px) {
  .container {
    max-width: 100%; } }

@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 36px;
    padding-right: 36px; } }

@media (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px; } }

h2 {
  font-size: 40px;
  line-height: 44px;
  color: #1a1a1a;
  font-weight: bold;
  position: relative; }
  @media (max-width: 576px) {
    h2 {
      font-size: 18px;
      line-height: 21px; } }

h3 {
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  font-weight: 600; }

h4 {
  font-size: 24px;
  line-height: 24px;
  letter-spacing: -1px;
  color: #000000;
  font-weight: 600; }

h6 {
  font-size: 14px;
  line-height: 24px;
  color: #000000;
  font-weight: 600;
  letter-spacing: 1px; }

a {
  outline: none; }

button, input, textarea, span, div, a {
  outline: none !important; }
  button:hover, input:hover, textarea:hover, span:hover, div:hover, a:hover {
    outline: none !important; }
  button:focus, input:focus, textarea:focus, span:focus, div:focus, a:focus {
    outline: none !important; }
  button:active, input:active, textarea:active, span:active, div:active, a:active {
